import chanpinyaosu from "@/components/productShow/chanpinyaosu-show.vue"
import machineStatus from "@/components/productShow/machineStatus.vue"
import uniIcon from "@/components/uni-icons/uni-icons.vue";
let _this
export default {
	components:{
		chanpinyaosu,
		machineStatus,
		uniIcon
	    
	},
	data() {
		return {
			goodsCode:'',
			goodsDetail:'',
			qualityPosition:[],
			
		};
	},
	onShow:function(){
		this.getGoodsDetail()
	},
	onLoad:function(options){
		_this = this
		this.goodsCode = options.goodsCode
		console.log(options)
		this.getGoodsDetail()
		this.getQualityPosition()
	},
	methods:{	
		async getQualityPosition(){
			const res = await this.$http.get("/choose/quality",{})
			console.log(res)
			this.qualityPosition = res.data.data.list
		},
		getGoodsDetail:function(){
			let url = _this.Api.goodsDetails
			let data ={
				goodsCode: _this.goodsCode,  // 产品编码 
			}
			uni.showLoading({
				title:'加载中',
				mask: true
			})
			this.myRequest(data,url,'get').then(res => {
			      console.log(res)
				  if(res.data.status==0){
					  uni.hideLoading()
					  _this.goodsDetail = res.data.data
					  console.log(_this.goodsDetail)
				  }
				  
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
		},
		toBigPic:function(src){
			
			// this.src = src
			// this.$refs.bigimage.show()
			uni.previewImage({
				urls: [src],
				success:function(){
					console.log('正在预览中')
				},
				fail:function(){
					
				},
				complete:function(){
					
				}
			})
			
		},
		showModifyRecord:function(e){
			uni.navigateTo({
				url: './updateRecord?goodsCode='+e,
				success: res => {},
				fail: (err) => { console.log(err)},
				complete: () => {}
			});
		},
		soldOut:function(){
		    uni.showModal({
		    	title: '提示',
		    	content: '确认是否下架',
		    	showCancel: true,
		    	cancelText: '取消',
		    	confirmText: '下架',
		    	success: res => {
					if (res.confirm){
						let url = _this.Api.soldOut
						let data ={
							goodsCode: _this.goodsCode,  // 产品编码 
						}
						uni.showLoading({
							title:'提交中',
							mask: true
						})
						this.myRequest(data,url,'get').then(res => {
						      console.log(res)
							  if(res.data.status==0){
								  uni.hideLoading()
								  this.getGoodsDetail()
							  }
							  
						}).catch(err => {
						  wx.showToast({
						    title: err.data.errMsg,
						    icon: 'none'
						  });
						});
					}
				},
		    	fail: () => {},
		    	complete: () => {}
		    });
			
			
		},
		soldIn:function(){
			let url = _this.Api.soldOut
			let data ={
				goodsCode: _this.goodsCode,  // 产品编码 
			}
			uni.showLoading({
				title:'提交中',
				mask: true
			})
			this.myRequest(data,url,'get').then(res => {
			      console.log(res)
				  if(res.data.status==0){
					  uni.hideLoading()
					  this.getGoodsDetail()
				  }
				  
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
		},
		toEdit:function(){
			let data = JSON.stringify(_this.goodsDetail)
			let qualityPosition = JSON.stringify(this.qualityPosition)
			uni.navigateTo({
				url: `./editProduct?goodsDetail=${data}&qualityPosition=${qualityPosition}`,
				success: res => {},
				fail: (err) => { console.log(err)},
				complete: () => {}
			});
		},
		submit:function(){
			let data = JSON.stringify(_this.goodsDetail)
			uni.navigateTo({
				url: './productUpdate?goodsDetail='+ data,
				success: res => {},
				fail: (err) => { console.log(err)},
				complete: () => {}
			});
		},
		copyProduct:function(){
			let data = JSON.stringify(_this.goodsDetail)
			let qualityPosition = JSON.stringify(this.qualityPosition)
			uni.navigateTo({
				url: `./productCreateCopy?goodsDetail=${data}&qualityPosition=${qualityPosition}&companyCode=${this.goodsDetail.companyCode}&companyName=${this.goodsDetail.companyName}`
			});
		}
	}
}
